<template>
  <div class="wrapper">
    <Header @filterNotes="searchVal = $event"/>
    <Notes :notes="noteFilter"
    @dellNote="dellNote"
    @editNote="editNote"
    />
    <Modal
     v-show="isModalOpen"
    @close="isModalOpen = false"
    @addNote="addNote"
    :edit="edit"
    @newTask="newTask"
    :editTask="editTask"
    />
    <button v-show="!isModalOpen" class="modal-btn" @click="isModalOpen = true, edit = false">
      <img src="@/assets/img/edit.svg" alt="">
    </button>
  </div>
</template>

<script>
import Header from '@/components/Header.vue'
import Notes from '@/components/Notes.vue'
import Modal from '@/components/Modal.vue'

  export default {
    components:{
      Header,
      Notes,
      Modal,
    },
    data() {
      return {
        notes:[],
        isModalOpen: false,
        edit: false,
        editTask: null,
        searchVal: ''
      }
    },
    methods: {
      addNote(note){
        this.notes.push(note)
      },
      dellNote(id){
        let index = this.notes.findIndex(note => note.id === id)
        this.notes.splice(index, 1)
      },
      getNote(){
        let localNotes = localStorage.notes
        if (localNotes) {
          this.notes = JSON.parse(localNotes)
        }
      },
      editNote(id){
         this.isModalOpen = this.edit = true
         let currentNote = this.notes.find(el => el.id == id)
         this.editTask = currentNote
      },
      newTask(newNote){
        this.notes.forEach(note => {
          if (note.id == newNote.id) {
            note.title = newNote.title
            note.text = newNote.text
            note.date = newNote.date
          }
        })
      },
    },
    created() {
      this.getNote()
    },
    watch:{
      notes:{
        handler(){
          localStorage.notes = JSON.stringify(this.notes)
        },
        deep: true
      }
    },
    computed:{
      noteFilter(){
        return this.searchVal ? this.notes.filter(note => note.title.toLowerCase().includes(this.searchVal.toLowerCase())) : this.notes
      }
    }
  }
</script>

