<template>
    <div class="notes__item">
        <div class="notes__item-top" :class="{active: !view}">
            <h3 class="notes__item-top-title">{{ note.title }}</h3>
            <p class="notes__item-top-date">{{ note.data }}</p>
        </div>
        
        <p class="notes__item-text">{{ note.text }}</p>
        
        <div class="notes__item-btns">
            <button class="notes__item-btn edit" @click="$emit('editNote', note.id)">
                <img src="@/assets/img/edit.svg" alt="">
                <span>Редактировать</span>
            </button>
            <button class="notes__item-btn dell" @click="$emit('dellNote', note.id)">
                <img src="@/assets/img/dell.svg" alt="">
                <span>Удалить</span>
            </button>
        </div>
    </div>
</template>

<script>
    export default {
        props:{
            note:{
                typeof: Object
            },
            view:{
                typeof: Boolean
            }
        }
    }
</script>

